.landing-page-container {
  width: 100%;
  height: 100%;
  grid-column-start: 1;
  grid-column-end: 3;
  --logo-landing-time: 600ms;
  --text-landing-time: 500ms;
}

.hero-image {
  width: 100%;
  height: 100%;
  position: relative;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(36, 36, 36, 0.5)),
    url(/static/media/hero-image.fcc35856.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-size: 100% 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@keyframes slide-on-load {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.hero-image h1 {
  font-size: clamp(5em, 2.5vw, 10em);
  color: #fff;
  word-spacing:-20px;
  word-wrap:unset;
 
  text-shadow: 5px 5px 5px #000;
  animation: slide-on-load var(--logo-landing-time) ease-out;
}

.sharpen-red {
  color: var(--color-primary);
  font-style: italic;
  font-size: larger;
  text-shadow: none;
}

.hero-image p {
  opacity: 0;
  transform: translateY(-200%);
  animation: fade-in var(--text-landing-time) ease-in var(--logo-landing-time) forwards;
  margin: 1em 1em 0 1em;
}

.hero-image p, .hero-image a {
  font-size: larger;
  color: whitesmoke !important;
}


.hero-image .getStarted {
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 1.3em;
  color: var(--color-secondary);
  background: transparent;
  text-decoration: none;
  border: 2px solid var(--color-secondary);
  padding: 10px 20px;
  cursor: pointer;
  transition: 500ms;
  opacity: 0;
  animation: fade-in var(--text-landing-time) ease-in var(--logo-landing-time) forwards;
}

.hero-image .getStarted:focus, .hero-image .getStarted:hover {
  outline: none;
  box-shadow: 0px 0px 15px white;
}

.hero-image .getStarted:active {
  transform: scale(.8);
}

@media (max-width: 500px) {
  .hero-image h1 {
    font-size: clamp(2.5em, 2.5vw, 4em);
    margin-bottom: 3em;
  }
}
